@media screen and (min-width: 320px) {
  .static-container.-about {
    display: flex;
    align-items: center;
    flex-direction: column;
    background: #000000;
    padding: 50px 20px; }
    .static-container.-about > .title-wrap {
      display: none;
      flex-direction: column;
      color: #ffffff;
      margin-right: 40px; }
    .static-container.-about > .title-wrap > .title {
      color: #ffffff; }
    .static-container.-about > .title-wrap > .text > .part {
      display: block;
      margin-bottom: 15px; }
    .static-container.-about > .wrap {
      display: flex;
      align-items: flex-end;
      width: 100%; }
    .static-container.-about > .wrap > .img {
      width: 100%; }
    .static-container.-about > .text {
      margin-left: 30px;
      color: white; }
    .static-container.-about > .title-wrap.-mobile {
      display: flex;
      flex-direction: column;
      margin: 0; }
    .static-container.-about > .title-wrap.-mobile > .text {
      margin-top: 30px;
      text-align: center; } }

@media screen and (min-width: 1000px) {
  .static-container.-about {
    display: flex;
    align-items: flex-start;
    background: #000000;
    padding: 50px 30px 50px 70px;
    flex-direction: row; }
    .static-container.-about > .title-wrap.-mobile {
      display: none; }
    .static-container.-about > .title-wrap {
      display: flex;
      text-align: left; }
    .static-container.-about > .title-wrap > .text {
      text-aling: left; }
    .static-container.-about > .wrap {
      margin-top: 90px;
      width: auto; }
    .static-container.-about > .wrap > .img {
      width: 500px; } }
