body {
  margin: 0; }

.coming-soon {
  position: relative;
  width: 100%;
  height: 100vh;
  background: #000000 url("/static/img/coming-soon.jpg") no-repeat;
  background-position: center; }
  .coming-soon > .link {
    position: absolute;
    top: 65%;
    left: 50%;
    transform: translate(-70%, -50%);
    text-decoration: none;
    background-color: #ffa500;
    padding: 10px;
    text-transform: uppercase; }
    .coming-soon > .link:focus {
      text-decoration: none; }
    .coming-soon > .link:hover {
      background-color: #ffffff; }
    .coming-soon > .link:hover > .title {
      color: #ffa500; }
    .coming-soon > .link:focus {
      text-decoration: none; }
  .coming-soon > .link > .title {
    font-size: 20px;
    color: #ffffff;
    text-decoration: none;
    margin-bottom: 0; }
